.aligncenter {
  display: flex;
  flex-direction: column;
  align-self: center;
}

.main-title {
  line-height: 1;
  margin-bottom: 15px;
  color: #555;
}



.image-container {
  position: relative;
  width: 100%;
  max-width: 400px;
  margin: auto;
  overflow: hidden;
  border-radius: 15px;
  transition: transform 0.3s;
}

.image-container:hover {
  transform: scale(1.05);
}

.portfolio-image {
  display: block;
  width: 100%;
  height: auto;
  transition: transform 0.3s ease;
}

.image-container:hover .portfolio-image {
  transform: scale(1.1);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
}

.image-container:hover .overlay {
  opacity: 1;
}

.text {
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
}