.main-section {
  /* padding: 50px; */
  background-color: #fff;
  color: #555;
}

.timeline {
  font-size: 16px;
  margin-left: -50px;
}

.timeline_element {
  position: relative;
}

.timeline_element,
.timeline_element-contents {
  overflow: hidden;
}

.timeline_element-contents {
  padding: 30px 0 30px 10px;
  margin-left: 160px;
}

@media screen and (max-width: 567px) {
  .timeline_element-date {
    color: #999;
  }
  .timeline_element-contents {
    margin-left: 0;
    padding: 15px 0 0;
  }
  .timeline_element {
    border-top: 1px solid #ddd;
    padding-top: 15px !important;
    margin-top: 15px !important;
  }
  .timeline > ul,
  .timeline > ul > li {
    margin: 5px;
  }
}

@media screen and (min-width: 568px) {
  .timeline_element-date {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 155px;
    padding: 30px 15px;
    color: #999;
    text-align: right;
    font-style: italic;
    line-height: 30px;
    border-right: 1px solid #ddd;
  }
  .timeline_element-date:after {
    content: " ";
    position: absolute;
    top: 42.5px;
    right: -2.5px;
    display: block;
    width: 5px;
    height: 5px;
    background-color: rgba(187, 30, 30, 0.8);
  }
  .timeline.portfolio-timeline
    .timeline_element:first-child
    .timeline_element-contents,
  .timeline_element:first-child .timeline_element-contents {
    padding-top: 0;
  }

  .timeline.portfolio-timeline
    .timeline_element:first-child
    .timeline_element-date:after {
    top: 0;
  }
  .timeline.portfolio-timeline
    .timeline_element:first-child
    .timeline_element-date {
    padding-top: 0;
    line-height: 0.5;
    margin-top: 10px;
  }
  .timeline > ul,
  .timeline > ul > li {
    margin: 0px;
    padding: 0px;
  }
  .timeline_element + .timeline_element .timeline_element-contents {
    border-top: 1px solid #ddd;
  }
}

.portfolio-timeline .timeline_element.timeline_element--now .project-text {
  width: auto;
  float: none;
}

@media screen and (max-width: 1000px) {
  .main-section {
    padding: 10px 5px;
    margin: 0 auto;
  }
}

.social-links li {
  list-style: none;
  display: inline-block;
}

.social-links li a {
  width: 50px;
  float: left;
  position: relative;
  color: rgba(175, 34, 34, 0.8);
}

.social-links li a ion-icon {
  position: absolute;
  font-size: 26px;
  left: 25%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  transition: all 0.3s;
}

li::marker {
  color: rgba(175, 34, 34, 0.8);
  /* content: "â–º"; */
}

.task > ul > li {
  padding-left: 5px;
  max-width: 60em;
  text-align:justify;

}

